<template>
  <div class="user-container">
    <!-- 用户基本信息面板 -->
    <div class="user-card">
      <!-- 用户头像、姓名 -->
      <van-cell>
        <!-- 使用 title 插槽来自定义标题 -->
        <template #icon>
          <img :src="$store.state.userPhoto" alt class="avatar" />
        </template>
        <template #title>
          <span class="username">{{userList.name}}</span>
        </template>
        <template #label>
          <van-tag color="#fff" text-color="#007bff">申请认证</van-tag>
        </template>
      </van-cell>
      <!-- 动态、关注、粉丝 -->
      <div class="user-data">
        <div class="user-data-item">
          <span>{{userList.art_count}}</span>
          <span>动态</span>
        </div>
        <div class="user-data-item">
          <span>{{userList.fans_count}}</span>
          <span>关注</span>
        </div>
        <div class="user-data-item">
          <span>{{userList.follow_count}}</span>
          <span>粉丝</span>
        </div>
        <div class="user-data-item">
          <span>{{userList.like_count}}</span>
          <span>获赞</span>
        </div>
      </div>
    </div>

    <!-- 操作面板 -->
    <van-cell-group class="action-card">
      <van-cell icon="edit" title="编辑资料" is-link to="/user_edit" />
      <van-cell icon="chat-o" title="小思同学" is-link to="/chat" />
      <van-cell @click="outUser" icon="warning-o" title="退出登录" is-link />
    </van-cell-group>
  </div>
</template>

<script>
import { getUserApi } from '@/api'
import { Dialog } from 'vant'
import { removeStorage } from '@/util/storage'
import { mapMutations } from 'vuex'
export default {
  name: 'layoutUsers',
  data () {
    return {
      // 用户数据
      userList: {}
    }
  },
  methods: {
    // 映射
    ...mapMutations([ 'SET_USERPHOTO' ]),

    async userListFn () {
      const { data: res } = await getUserApi()
      this.userList = res.data
      // 图片路径存入vuex
      this.SET_USERPHOTO(res.data.photo)

      console.log(res.data)
    },
    // 退出登录
    outUser () {
      Dialog.confirm({
        title: '退出登录',
        message: '这就走了?不爱我了吗?'
      })
        .then(() => {
          // on confirm
          removeStorage('geek-itheima')
          this.$router.replace('/login')
        })
        .catch(() => {
          // on cancel
        })
    }
  },
  created () {
    this.userListFn()
  }

}
</script>

<style scoped lang="less">
.user-container {
  .user-card {
    background-color: #007bff;
    color: white;
    padding-top: 20px;
    .van-cell {
      background: #007bff;
      color: white;
      &::after {
        display: none;
      }
      .avatar {
        width: 60px;
        height: 60px;
        background-color: #fff;
        border-radius: 50%;
        margin-right: 10px;
      }
      .username {
        font-size: 14px;
        font-weight: bold;
      }
    }
  }
  .user-data {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-size: 14px;
    padding: 30px 0;
    .user-data-item {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 33.33%;
    }
  }
}
</style>
